import React, {useRef, useState, useEffect} from 'react'
import imgStart from './medias/imgStart.jpg'

export default function Starter({isFirst, classText, padding}) {

    const el1 = useRef();

    const init = window.innerHeight / 3

    const [reveal,
        setReveal] = useState("")
    const [translate,
        setTranslate] = useState(init)

    useEffect(() => {

        setTimeout(() => {
            setReveal('reveal')
        }, 500);

        window.addEventListener('scroll', function () {
            const pos1 = el1.current.offsetTop - window.scrollY
            pos1 < window.innerHeight
                ? setTranslate(init - pos1/5)
                : setTranslate(init)
        })
    })

    function setPosition() {
        let response = "3rem"
        isFirst ? response = "5rem" : response = "3rem"
        return response
    }
    console.log(setPosition())

    return (
        <div ref={el1} style={{padding: `${padding}`}} className="starter">
            <img className="cover" src={imgStart} alt="img"/>
            <div
                style={{
                transform: `translateY(${translate}px)`
            }}
                className={`text-starter hidden ${reveal}`}>
                <h1 style={{fontSize: setPosition()}} className={`title ${classText}`}>On the other hand</h1>
                <p className="paragraph">But I must explain to you how all this mistaken idea of
                    denouncing pleasure and praising pain was born and I will give you a complete
                    account of the system, and expound the actual teachings of the great explorer</p>
                <div className="btn">Discover</div>
            </div>
        </div>
    )

}